.footer {
   @media (max-width: 768px) {
      height: 100px;
   }

   display: flex;
   justify-content:center;
   align-items: center;
   background-color: #F7F7F7;
   margin-top: auto;
   color: #545454;
   width: 100%;
   height: 77px;
   z-index: 2;

   .footer-content {
      font-family: 'Roboto';
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 16px;
      display: flex;
      justify-content: center;

      @media (max-width: 768px) {
         flex-direction: column;
         align-items: center;
      }

      .content-one p {
         margin: 0;
      }

      .content-two {
         display: flex;
         align-items: baseline;

         & p {
            margin: 0;
         }

         .icon-heart {
            width: 14px;
            height: 12.88px;
            fill: #FF6B08;
            margin-left: 10px;
            margin-right: 10px;

            animation-name: slidein;
            animation-duration: 1s;
            animation-iteration-count: infinite;
         }

         @keyframes slidein {
            from {
               opacity: 1;
               scale: 1.2;
            }

            50% {
               opacity: 0.9;
               scale: 1;
            }

            to {
               opacity: 0.85;
               scale: 1.1;
            }
         }


         .team-link {
            text-decoration: underline;
            color: inherit;
         }
      }
   }
}